.container {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 695px;
}

.current-img {
    width: 100%;
    height: 100%;
    /* border-radius: 0.5rem; */
    box-shadow: 0px 0px 7px #6666;
}

.hide {
    display: none;
}

.arrow {
    position: absolute;
    width: 2rem;
    height: 2rem;
    color: #fff;
    filter: drop-shadow(0px 0px 5px #555);
}

.arrow-left {
    left: 1rem;
}

.arrow-right {
    right: 1rem;
}

.circle-indicators {
    display: flex;
    position: absolute;
    bottom: 1rem;
}

.current-indicator {
    background-color: #ffffff;
    height: 15px;
    width: 15px;
    border-radius: 50%;
    border: none;
    outline: none;
    margin: 0 0.2rem;
    cursor: pointer;
}

.inactive {
    background-color: grey;
}